import moment from "moment";
import IMAGES from "../../../assets/images";

export default function SpecialistLetterPDF({
  specialistLetter,
  detailDTO,
  getUserData,
}) {
  return (
    <div className="mb-5">
      <div className="d-flex justify-content-center mb-4">
        <img
          src={IMAGES.LOGO}
          alt="FamilyDoc 24/7 Logo"
          style={{ width: "22%" }}
        />
      </div>
      <div className="text-center">
        <h6>General Practitioner (GP) / Family Physician</h6>
        <h6>77 Camden Street Lower, Dublin 2</h6>
        <h6>Tel: +353 1 906 9327</h6>
        <h6>Email : contact@familydoc247.ie</h6>
      </div>
      <p className="my-5">
        {specialistLetter?.date
          ? moment(specialistLetter?.date).format("MM/DD/YYYY")
          : "N/A"}
      </p>
      <p className="mb-5">
        <span className="font-weight-600">To: </span>
        {specialistLetter?.toDoctor}
      </p>
      <div className="d-flex">
        <p className="pe-2">
          <span className="font-weight-600">Re: </span>
          {specialistLetter?.patient ? specialistLetter?.patient : "N/A"}
        </p>
        <p className="pe-2">
          <span className="font-weight-600">Address: </span>
          {detailDTO?.currentAddress ? detailDTO?.currentAddress : "N/A"}
        </p>
        <p>
          <span className="font-weight-600">DOB: </span>
          {moment(detailDTO?.dob ? detailDTO?.dob : "N/A").format("MM/DD/YYYY")}
        </p>
      </div>
      <p>
        <span className="font-weight-600">Tel: </span>
        {detailDTO?.phoneNumber ? detailDTO?.phoneNumber : "N/A"}
      </p>
      <p className="mt-5">Dear Doctor,</p>
      <p>I would appreciate if you could see above-named patient.</p>
      <div className="mb-5">
        {specialistLetter?.referralNote && (
          <div>
            <p className="text-black-50 mb-0">Referral Notes</p>
            {specialistLetter?.referralNote}
          </div>
        )}
        {specialistLetter?.appointmentDetail && (
          <>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Presenting Complaint</p>
              {specialistLetter?.appointmentDetail?.presentingComplaint || "N/A"}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Past Medical History</p>
              {specialistLetter?.appointmentDetail?.pastMedicalHistory || "N/A"}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Medications</p>
              {specialistLetter?.appointmentDetail?.medications || "N/A"}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Allergies</p>
              {specialistLetter?.appointmentDetail?.allergies || "N/A"}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Family History</p>
              {specialistLetter?.appointmentDetail?.familyHistory || "N/A"}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Social History</p>
              {specialistLetter?.appointmentDetail?.socialHistory || "N/A"}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Objective Examination</p>
              {specialistLetter?.appointmentDetail?.objectiveExamination || "N/A"}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Suspected Diagnosis</p>
              {specialistLetter?.appointmentDetail?.suspectedDiagnosis || "N/A"}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Recommended Actions</p>
              {specialistLetter?.appointmentDetail?.recommendedActions || "N/A"}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Sign off</p>
              {specialistLetter?.appointmentDetail?.signOff || "N/A"}
            </div>
          </>
        )}
      </div>
      <p className="pb-3">
        The above-named patient prefers to see you privately.
      </p>
      <p className="pb-3">
        I would appreciate your expert review and further management.
      </p>
      <p className="pb-2">Yours sincerely,</p>
      <div className="d-flex">
        <div className="me-5">
          <p className="m-0">
            MCRN {getUserData?.mcrn ? getUserData?.mcrn : "N/A"}
          </p>
          <p className="m-0">
            Dr {getUserData?.name ? getUserData?.name : "N/A"}
          </p>
        </div>

        {getUserData?.doctorSignature ? (
          <span
            style={{
              display: "inline-block",
              width: "100px",
              height: "50px",
            }}
          >
            <img
              src={getUserData?.doctorSignature}
              alt="signature"
              width="140px"
              height="100px"
            />
          </span>
        ) : (
          <p className="text-decoration-underline">No Signature Found</p>
        )}
      </div>
    </div>
  );
}
