import moment from "moment";
import IMAGES from "../../../assets/images";

export default function EmergencyLetterPDF({
  emergancyDepartmentLetter,
  detailDTO,
  getUserData,
}) {
  return (
    <div className="mb-5">
     <div className="d-flex justify-content-center mb-4">
        <img
          src={IMAGES.LOGO}
          alt="FamilyDoc 24/7 Logo"
          style={{ width: "22%" }}
        />
      </div>
      <div className="text-center">
        <h6>General Practitioner (GP) / Family Physician</h6>
        <h6>77 Camden Street Lower, Dublin 2</h6>
        <h6>Tel: +353 1 906 9327</h6>
        <h6>Email : contact@familydoc247.ie</h6>
      </div>
      <p className="my-5">
        {emergancyDepartmentLetter?.date
          ? moment(emergancyDepartmentLetter?.date).format("MM/DD/YYYY")
          : "N/A"}
      </p>
      <p className="mb-5">
        <span className="font-weight-600">To: </span>
        Emergency Department Doctor On Duty
      </p>
      <div className="d-flex">
        <p className="pe-2">
          <span className="font-weight-600">Re: </span>
          {emergancyDepartmentLetter?.patient
            ? emergancyDepartmentLetter?.patient
            : "N/A"}
        </p>
        <p className="pe-2">
          <span className="font-weight-600">Address: </span>
          {detailDTO?.currentAddress ? detailDTO?.currentAddress : "N/A"}
        </p>
        <p>
          <span className="font-weight-600">DOB: </span>
          {detailDTO?.dob ? moment(detailDTO?.dob).format("MM/DD/YYYY"): "N/A"}
        </p>
      </div>
      <p>
        <span className="font-weight-600">Tel: </span>
        {detailDTO?.phoneNumber ? detailDTO?.phoneNumber : "N/A"}
      </p>
      <p className="mt-5">Dear Doctor On Duty,</p>
      <p>I would appreciate if you could see above-named patient.</p>
      <div className="mb-5">
        {emergancyDepartmentLetter?.referralNote && (
          <div>
            <p className="text-black-50 mb-0">Referral Notes</p>
            {emergancyDepartmentLetter?.referralNote}
          </div>
        )}
        {emergancyDepartmentLetter?.appointmentDetail && (
          <>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Presenting Complaint</p>
              {
                emergancyDepartmentLetter?.appointmentDetail
                  ?.presentingComplaint || "N/A"
              }
            </div>
            {/* <div className="mb-3">
              <p className="text-black-50 mb-0">
                History of presenting complaint
              </p>
              {
                emergancyDepartmentLetter?.appointmentDetail
                  ?.historyOfPresentingComplaint
              }
            </div> */}
            <div className="mb-3">
              <p className="text-black-50 mb-0">Past Medical History</p>
              {emergancyDepartmentLetter?.appointmentDetail?.pastMedicalHistory || "N/A"}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Medications</p>
              {emergancyDepartmentLetter?.appointmentDetail?.medications || "N/A"}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Allergies</p>
              {emergancyDepartmentLetter?.appointmentDetail?.allergies || "N/A"}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Family History</p>
              {emergancyDepartmentLetter?.appointmentDetail?.familyHistory || "N/A"}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Social History</p>
              {emergancyDepartmentLetter?.appointmentDetail?.socialHistory || "N/A"}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Objective Examination</p>
              {emergancyDepartmentLetter?.appointmentDetail?.objectiveExamination || "N/A"}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Suspected Diagnosis</p>
              {emergancyDepartmentLetter?.appointmentDetail?.suspectedDiagnosis || "N/A"}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Recommended Actions</p>
              {emergancyDepartmentLetter?.appointmentDetail?.recommendedActions || "N/A"}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Sign off</p>
              {emergancyDepartmentLetter?.appointmentDetail?.signOff || "N/A"}
            </div>
          </>
        )}
      </div>

      <p className="pb-3">
        I would appreciate your urgent assessment and management.
      </p>
      <p className="pb-2">Yours sincerely,</p>

      <div className="d-flex">
        <div className="me-5">
          <p className="m-0">
            MCRN {getUserData?.mcrn ? getUserData?.mcrn : "N/A"}
          </p>
          <p className="m-0">
            Dr {getUserData?.name ? getUserData?.name : "N/A"}
          </p>
        </div>
        {getUserData?.doctorSignature ? (
          <span
            style={{
              display: "inline-block",
              width: "100px",
              height: "50px",
            }}
          >
            <img
              src={getUserData?.doctorSignature}
              alt="signature"
              width="140px"
              height="100px"
            />
          </span>
        ) : (
          <p className="text-decoration-underline">No Signature Found</p>
        )}
      </div>
    </div>
  );
}
